.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}



/* Header */
* {
  margin: 0px;
  padding: 0px;
}

html,
body {
  height: 100%;
  width: 100%;
}

header {
  background-color: #0DCAF0;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #ffffff;
}


/* Conteudo */

main {
  margin-left: 20%;
  margin-top: 25px;
  width: 80%;
  font-family: Arial, Helvetica, sans-serif;
  height: 100%;
  display: flex;
  align-items: center;
}

main h2 {
  position: absolute;
}

.cardizin {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
  max-width: 90%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}

.cardizinho {
  margin: 15px;
  width: 350px;
  height: 550px;
  border: 1px solid #D2D2D2;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}


.desc {
  color: rgb(121, 120, 120);
  font-size: 15px;
  display: flex;
  position: relative;
  top: 30px;
  left: 10px;
}

.carrinho {
  margin-top: 65px;
  background-color: #D1E7DD;
  border-radius: 5px;
  width: 300px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  font-size: 14px;
  display: none;

}


button {
  background-color: #0D6EFD;
  color: #fff;
  border: 0px;
  border-radius: 5px;
  height: 30px;
  width: 150px;
  position: relative;
  top: 50px;
  left: 10px;
  align-items: center;
}


@media screen and (max-width: 1024px) {
  header {
    background-color: #0DCAF0;
    height: 100px;
    width: 110%;
    display: flex;
    justify-content: space-around;
    color: #ffffff;
  }

  header h1 {
    width: 200px;
  }

  .cardizin {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    margin-left: -20px;
    max-width: 80%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

  }

  .cardizinho {
    margin: 15px;
    width: 350px;
    height: 500px;
    border: 1px solid #D2D2D2;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
  }

  .carrinho {
    display: none;
  }

}